import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
} from 'pure-react-carousel';
import Img from 'gatsby-image';
import Box from 'components/box';
import Title from 'components/title';
import Button from 'components/button';
import Header from 'components/header';
import Wrapper from 'components/wrapper';
import bgImg from 'images/banner_bg.png';
import { Carousel, Section } from './headSection.css';

class HeadSection extends Component {
  state = { slidesAmount: 2.5 };

  componentDidMount() {
    const slidesAmount = window && window.innerWidth <= 767 ? 1 : 2.5;

    this.setState({
      slidesAmount,
    });
  }

  render() {
    const { data } = this.props;
    const { slidesAmount } = this.state;

    return (
      <Section img={bgImg}>
        <Header altImage={data.headerImageAlt} />
        <Wrapper>
          <Box className="padding-15">
            <div className="box-tags">
              <div className="box-tag">
                <span className="box-tag-icon">icon</span>
                <span className="medium">{data.days}</span>
              </div>
              <div className="box-tag">
                <span className="box-tag-icon">icon</span>
                <span className="medium">{data.country}</span>
              </div>
            </div>
            <Title as="h2" size="large">
              {data.title}
            </Title>
            <div className="box-price">
              <span className="large">$ {data.cost}</span>
              <br />
              <span className="small italic">{data.perPerson}</span>
            </div>
            <Button type="primary">{data.more}</Button>
          </Box>
        </Wrapper>
        <Carousel>
          <CarouselProvider
            naturalSlideWidth={253}
            naturalSlideHeight={139}
            visibleSlides={slidesAmount} // eslint-disable-line no-undef
            dragEnabled={false}
            totalSlides={data.headCarousel.length}
          >
            <div className="carousel-flex-wrapper">
              <div className="flex-space-holder" />
              <div className="slider-nav-btn-wrapper">
                <ButtonBack />
                <ButtonNext />
              </div>
              <div className="flex-slider-wrapper">
                <Slider>
                  {data.headCarousel.map((image, i) => (
                    <Slide
                      index={i}
                      key={JSON.stringify(image.childImageSharp.fluid.src)}
                      className="single-slide"
                    >
                      <Img
                        fluid={image ? image.childImageSharp.fluid : {}}
                        alt="single-img"
                      />
                    </Slide>
                  ))}
                </Slider>
              </div>
            </div>
          </CarouselProvider>
        </Carousel>
      </Section>
    );
  }
}

HeadSection.propTypes = {
  data: PropTypes.shape({
    headCarousel: PropTypes.array.isRequired,
    headerImageAlt: PropTypes.string.isRequired,
    days: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    cost: PropTypes.number.isRequired,
    perPerson: PropTypes.string.isRequired,
    more: PropTypes.string.isRequired,
  }).isRequired,
};

export default HeadSection;
