import React, { Component } from 'react';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Layout from 'components/layout';
import HeadSection from 'components/headSection';
import Description from 'components/description';
import SubtitleSection from 'components/subtitleSection';
import Schedule from 'components/schedule';
import AccentSection from 'components/accentSection';
import BookingDescription from 'components/bookingDescription';
import HtmlFromMarkdown from 'components/htmlFromMarkdown';
import Articles from 'components/articles';
import Mark from 'components/mark';
import Box from 'components/box';
import Wrapper from 'components/wrapper';
import Carousel from 'components/carousel';
import Planet from 'images/ico_planet.png';

export default class Main extends Component {
  state = {};

  render() {
    const { data } = this.props;
    const opts = data.homeJson;

    return (
      <Layout>
        <HeadSection data={opts} />
        <Wrapper>
          <SubtitleSection
            image={opts.descriptionImage}
            imageAlt={opts.descriptionAltTitle}
            title={opts.descriptionTitle}
          >
            <HtmlFromMarkdown markdown={opts.descriptionSubtitle} />
          </SubtitleSection>

          <Mark uppercase="true">
            <img width="17" src={Planet} alt="f" />
            <span>{opts.descriptionMark}</span>
          </Mark>
          <Description title={opts.descriptionContentTitle}>
            <HtmlFromMarkdown markdown={opts.descriptionContent} />
            <HtmlFromMarkdown markdown={opts.descriptionContent} />
          </Description>
        </Wrapper>
        {/* weather widget section */}
        <Wrapper>
          <Carousel images={opts.mainCarousel} />
          <SubtitleSection
            title={opts.itineraryTitle}
            image={opts.itineraryImage}
            imageAlt={opts.itineraryAltTitle}
          >
            <HtmlFromMarkdown markdown={opts.itineraryContent} />
          </SubtitleSection>
          <Schedule items={opts.schedule} />
        </Wrapper>
        <AccentSection>
          <Wrapper>
            <Box>
              <BookingDescription
                productId={opts.productId}
                title={opts.bookingTitle}
                persons={opts.bookingDays}
                days={opts.bookingPersons}
                subTitle={opts.selectSeats}
              >
                <HtmlFromMarkdown markdown={opts.bookingContent} />

                {/* <div>datapicker will be here</div> */}
              </BookingDescription>
            </Box>
          </Wrapper>
        </AccentSection>
        <Wrapper>
          <SubtitleSection
            image={opts.readingsImage}
            imageAlt={opts.readingsAltTitle}
            title={opts.readingsTitle}
          >
            <HtmlFromMarkdown markdown={opts.readingsContent} />
          </SubtitleSection>
          <Articles articles={opts.articles} />
        </Wrapper>
      </Layout>
    );
  }
}

export const query = graphql`
  query HomepageQuery {
    homeJson {
      headerImageAlt
      selectSeats
      productId
      title
      days
      country
      cost
      perPerson
      more
      descriptionAltTitle
      descriptionTitle
      headCarousel {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      mainCarousel {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      descriptionSubtitle {
        childMarkdownRemark {
          html
        }
      }
      descriptionMark
      descriptionContentTitle
      descriptionContent {
        childMarkdownRemark {
          html
        }
      }
      itineraryTitle
      itineraryAltTitle
      itineraryContent {
        childMarkdownRemark {
          html
        }
      }
      bookingTitle
      bookingDays
      bookingPersons
      bookingContent {
        childMarkdownRemark {
          html
        }
      }
      readingsTitle
      readingsAltTitle
      readingsContent {
        childMarkdownRemark {
          html
        }
      }
      schedule {
        duration
        number
        title
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        content {
          childMarkdownRemark {
            html
          }
        }
      }
      articles {
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        href
        title
        content {
          childMarkdownRemark {
            html
          }
        }
      }
      descriptionImage {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      itineraryImage {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      readingsImage {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  }
`;

Main.propTypes = {
  data: PropTypes.shape({
    homeJson: PropTypes.object.isRequired,
  }).isRequired,
};
