import React from 'react';
import PropTypes from 'prop-types';
import Title from 'components/title';
import Img from 'gatsby-image';
import { Section, ImageWrapper } from './subtitleSection.css';

const SubtitleSection = ({ image, imageAlt, title, children }) => (
  <Section  bgText={title}>
    <div className='title-wrapper'>
      <ImageWrapper>
        <Img fluid={image ? image.childImageSharp.fluid : {}} alt={imageAlt} />
      </ImageWrapper>
      <Title as='h3' size='medium'>
        {title}
      </Title>
    </div>
    <div className='lead'>
      {children}
    </div>
  </Section>
);

SubtitleSection.propTypes = {
  image: PropTypes.shape({
    childImageSharp: PropTypes.object,
  }).isRequired,
  imageAlt: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};

export default SubtitleSection;
